<template>
	<div id="choose-activity">
		<common-header :title='title'></common-header>
		<div class="activity-top" v-if="promInfo">
			<img v-if="promInfo.images && promInfo.images.length > 0" class="activity-img" :src="promInfo.images[0].imagePath" alt="" :data-id="promInfo.images[0].id"/>
			<div class="activity-text">
				<div>活动内容：{{promInfo.description}}</div>
				<div v-if="promInfo.type == 1">活动范围：全部会员</div>
				<div v-else-if="promInfo.type == 2">活动范围：新人会员</div>
				<div v-else>活动范围：</div>
				<div>活动时间：{{promInfo.startTime}}~{{promInfo.endTime}}</div>
			</div>
			<good-list :goodList='promInfo.goodses'></good-list>
		</div>
	</div>
</template>

<script>
import GoodList from '@/components/ChooseGift/GoodList'
import CommonHeader from '@/components/Header/CommonHeader'
	
export default {
	data(){
		return {
			title:'活动专区',
			promInfo:{
//				images:[{id:1,imagePath:'../../../static/demo/activity-banner.png'}],
//				title:'标题',
//				description:'描述',
//				type:1,
//				startTime:'11月21日',
//				endTime:'11月24日',
//				goodses:[
//					{
//						skuId:1,
//						spuId:1,
//						type:1,
//						name:'商品名',
//						originalPrice:100,
//						discountPrice:50,
//						discount:5,
//						imagePath:'/static/img/pic.603b63b.png',
//						isDelete:2,
//						isUp:1,
//						tags:'加盟',
//						isSoldOut:2,
//						promId:1,
//						commentCount:1,
//						praiseCount:100
//					},
//					{
//						skuId:2,
//						spuId:2,
//						type:1,
//						name:'酷美人 夜光石手链 男女款萤石手链手串手环饰品 男女款萤石手链手串手环饰品',
//						originalPrice:200,
//						discountPrice:100,
//						discount:5,
//						imagePath:'/static/img/pic.603b63b.png',
//						isDelete:2,
//						isUp:1,
//						tags:'自营',
//						isSoldOut:2,
//						promId:1,
//						commentCount:1,
//						praiseCount:100
//					}
//				]
			},
		}
	},
  components: {
  	GoodList,
  	CommonHeader
  },
  watch:{
    '$route' (to, from) {
  		this.getPromInfo();
  	}
  },
  created(){
  	this.getPromInfo();
  },
  methods:{
  	getPromInfo(){
      let id = this.$route.params.id
  		this.$Api.getPromInfo(id).then((res)=>{
  			if(res.q.s==0){
  				this.promInfo = res.q
  				
  			}
  		})
  	},
  },
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	
	#choose-activity{
		background:#fff;
		.activity-top{
			/*@include headerSpace;*/
			.activity-img{
				height: 4.0rem;
				width: 100%;
				border-bottom:1px solid $border-color;
			}
			.activity-text{
				font-size: 0.37rem;
				/*min-height: 2.93rem;*/
				padding: 0.4rem;
				color: $text-color2;
				border-bottom: 1px solid $border-color;
			}
		}
	}
</style>


